.hidden {
  display: none;
}

.active {
  font-weight: 700;
  color: #1f2038;
}

.arrow-down {
  transform: rotate(180deg);
}

.section-faq {
  max-width: 144rem;
  margin: 0 auto;
}

.faq {
  font-size: 1.4rem;
  line-height: 1.4rem;
  color: var(--text-gray-blue);
  font-weight: 400;
  background-color: var(--white);
  border-radius: 2.3rem;
  box-shadow: 0px 50px 50px -20px var(--shadow-blue);
  display: grid;
  grid-template-columns: 46rem 46rem;
  grid-template-rows: 25.4rem 25.4rem;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.faq__info-container {
  grid-column: 2/3;
  grid-row: 1/span 2;
  align-self: center;
  padding-right: 9.5rem;
}
.faq__heading {
  font-size: 3.2rem;
  line-height: 3.2rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--text-gray-blue-dark);
  margin-bottom: 4.5rem;
}
.faq__img--woman {
  transform: translate(-18%, -92%);
}
.faq__img--pattern {
  width: 90.2rem;
  transform: translate(-57%, 26%);
}
.faq__img--box {
  transform: translate(-48%, -107%);
  position: fixed;
  z-index: 99999;
}
.faq__info-list {
  display: block;
  list-style: none;
}
.faq__info-list-item {
  cursor: pointer;
  padding-bottom: 1.9rem;
  border-bottom: 1px solid var(--text-divider);
  margin: 1.6rem 0;
}
.faq__info-list-item:first-of-type {
  margin-top: 0;
}
.faq .arrow {
  margin-right: 2.42rem;
}
.faq__info-question-box {
  display: flex;
  justify-content: space-between;
}
.faq__info-question {
  transition: all 200ms ease-out;
}
.faq__info-question:hover {
  color: var(--orange);
}
.faq__info-answer {
  margin-top: 1.1rem;
}